<script setup>
    import TextInput from './TextInput.vue';

    const balance = ref(parseInt(getItem('balance')) || 0);
    const transactions = ref(JSON.parse(getItem('transactions') || "[]"));
    const descriptionInput = ref('');
    const sumInput = ref('');
    const typeTransaction = ref(false);
    const balanceClasses = {
        true: 'text-green-600',
        false: 'text-red-600'
    };


    function addTransaction() {
        typeTransaction.value ? balance.value -= parseInt(sumInput.value) : balance.value += parseInt(sumInput.value);
        
        transactions.value.push({ 
            title: descriptionInput.value, 
            sum: sumInput.value, 
            category: 'Продукты',
            type: typeTransaction.value, 
            time: getTime(), 
            date: getDate()
        });

        setItem('transactions', JSON.stringify(transactions.value));
        setItem('balance', balance.value);
        descriptionInput.value = '';
        sumInput.value = '';
    }

    function getTime() {
        const date = new Date();
        return `${date.getHours().toString().padStart(2, '0')}:${date.getMinutes().toString().padStart(2, '0')}`
    }

    function getDate() {
        const date = new Date();
        return `${date.getDate().toString().padStart(2, '0')}.${(date.getMonth() + 1).toString().padStart(2, '0')}.${(date.getFullYear()).toString().padStart(2, '0')}`;
    }
 
    function removeTransaction(id) {
        if (transactions.value[id].type) {
            balance.value += parseInt(transactions.value[id].sum);
        }
        else {
            balance.value -= parseInt(transactions.value[id].sum);
        }
        transactions.value.splice(id, 1);
        setItem('balance', balance.value);
        setItem('transactions', JSON.stringify(transactions.value));
    }

    function setItem(key, value) {
        if (process.client) {
            localStorage.setItem(key, value);
        }
    }

    function getItem(key) {
        if (process.client) {
            return localStorage.getItem(key);
        }
        return undefined;
    }

</script>
<template>
    <section class='flex flex-col items-center gap-8 bg-white p-4 rounded-lg'>
        <div class="text-center">
            <h3 class="text-2xl">Трекер доходов и расходов</h3>
            <h1 class="text-4xl font-bold">Баланс: <span :class="balanceClasses[balance >= 0]"> {{ balance >= 0 }} </span></h1>
        </div>
        <div class="flex flex-col gap-2">
            <TextInput 
                v-model="descriptionInput"
                title="Описание" />
            <TextInput 
                v-model="sumInput"
                title="Сумма"
            />
            <div>
                <div class="flex items-center mb-4">
                    
                </div>
                <div class="flex justify-between items-center">
                    <input 
                        v-model="typeTransaction"
                        :value="false"
                        id="radio1"
                        type="radio" 
                        class="w-4 h-4 text-blue-600 bg-gray-100 border-gray-300 focus:ring-blue-500 dark:focus:ring-blue-600 dark:ring-offset-gray-800 focus:ring-2 dark:bg-gray-700 dark:border-gray-600">
                    <label for="radio1" class="ms-2 text-sm font-medium text-gray-900">Доход</label>
                    <input 
                        v-model="typeTransaction" 
                        id="radio2" 
                        type="radio" 
                        :value="true"
                        class="w-4 h-4 text-blue-600 bg-gray-100 border-gray-300 focus:ring-blue-500 dark:focus:ring-blue-600 dark:ring-offset-gray-800 focus:ring-2 dark:bg-gray-700 dark:border-gray-600">
                    <label for="radio2" class="ms-2 text-sm font-medium text-gray-900">Расход</label>
                </div>
            </div>
            <button 
                type="button" 
                @click="addTransaction"
                class="text-white bg-[#050708] hover:bg-[#050708]/90 focus:ring-4 focus:outline-none focus:ring-[#050708]/50 font-medium rounded-lg text-sm px-5 py-2.5 text-center inline-flex items-center dark:focus:ring-[#050708]/50 dark:hover:bg-[#050708]/30 me-2 mb-2">
                Добавить транзакцию
            </button>
        </div>
        
        <div class="relative overflow-x-auto">
            <h3 class="text-2xl font-bold text-center mb-2">История транзакций</h3>
            <table class="w-full text-sm text-left text-gray-500">
                <thead class="text-xs text-gray-700 bg-gray-50">
                    <tr>
                        <th class="px-6 py-3 text-center">
                            Описание
                        </th>
                        <th class="px-6 py-3 text-center">
                            Категория
                        </th>
                        <th class="px-6 py-3 text-center">
                            Сумма
                        </th>
                        <th class="px-6 py-3 text-center">
                            Дата
                        </th>
                        <th class="px-6 py-3 text-center">
                            Действие
                        </th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-if="transactions.length" v-for="(transaction, id) in transactions" class="bg-white border-b">
                        <td class="px-6 py-4 text-center">
                            {{ transaction.title }}
                        </td>
                        <td class="px-6 py-4 text-center">
                            {{ transaction.category }}
                        </td>
                        <td class="px-6 py-4 text-center" :class="balanceClasses[!transaction.type]">
                            {{ transaction.type ? '-' : '+' }}{{ transaction.sum }}
                        </td>
                        <td class="px-6 py-4 text-center">
                            {{ transaction.time }} {{ transaction.date }}
                        </td>
                        <td class="flex justify-center px-6 py-4">
                            <button @click="removeTransaction(id)">
                                &#9746;
                            </button>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    </section>
</template>
